<template>
	<view class="container">
		<view class="user-info">
			<uni-list>
				<uni-list-item showArrow title="头像" :clickable='true' @click="upload">
					<template v-slot:footer>
						<image class="avatar" :src="userInfo.avatar?userInfo.avatar:'/static/images/user.jpg'" ></image>
					</template>
				</uni-list-item>
				<uni-list-item title="用户名" :rightText="userInfo.username" />
				<uni-list-item showArrow :clickable='true' @click="navTo(`/pages/updata/setNickname?nickname=${userInfo.nickname}`)" title="昵称"
					:rightText="userInfo.nickname?userInfo.nickname:'未设置'" />
				<uni-list-item showArrow :clickable='true' @click="navTo(`/pages/updata/setMobile?mobile=${userInfo.mobile}`)" title="手机"
					:rightText="userInfo.mobile?userInfo.mobile:'未设置'" />
				<uni-list-item showArrow :clickable='true' @click="navTo(`/pages/updata/setEmail?email=${userInfo.email}`)" title="邮箱"
					:rightText="userInfo.email?userInfo.email:'未设置'" />
				<uni-list-item showArrow :clickable='true' @click="navTo('/pages/updata/setPassword')" title="修改密码" />
			</uni-list>
		</view>
		<view class="versions-box">
			<uni-list>
				<uni-list-item title="版本" rightText="1.0.0" />
			</uni-list>
		</view>
		<button class="logOut" @click="logout">退出登录</button>
	</view>
</template>

<script>
	import {
		mapMutations,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
			
			}
		},
		computed: {
			...mapState(['userInfo'])
		},
		methods: {
			...mapMutations(['logout', 'setUserInfo']),
			upload() {
				uni.chooseImage({
					count: 1,
					success: (res) => {
						let filePath = res.tempFilePaths[0];
						this.uploadAvatar(filePath, this.userInfo.id)
					}
				});
			},
			async uploadAvatar(filePath, userId) { //头像上传
				const response = await this.$api.user.uploadAvatar(filePath, userId)
				let data = response.data
				if (data.code == 200) {
					let userInfo = this.userInfo
					userInfo.avatar = data.data
					this.setUserInfo(userInfo)
					uni.showToast({
						title: data.message,
						icon: 'success',
						duration: 2000
					});
				}
			},
			navTo(url) {
				uni.navigateTo({
					url: url
				})
			},
			async checkToke(){ //效验token
				const response = await this.$api.user.checkToke()
			}
		},
		onShow() {
			this.checkToke()
		}
	}
</script>

<style lang="scss">
	.container {
		.avatar {
			width: 75upx;
			height: 75upx;
			border-radius: 50%;
		}

		/deep/.uni-list-item__container {
			align-items: center;
		}

		/deep/.uni-list-item__content-title {
			// 使用/deep/对组件内样式修改
			font-size: 28upx;
		}

		/deep/.uni-list-item__extra-text {
			font-size: 26upx;
		}

		.versions-box {
			margin-top: 10upx;
		}

		.logOut {
			margin-top: 30upx;
			width: 100%;
			height: 100upx;
			line-height: 100upx;
			background-color: #fff;
			font-size: 30upx;
			color: #009000;
		}
	}
</style>